import React from 'react'
import { Layout, Stack, Main, PreFooter, Sidebar } from '@layout'
import PageTitle from '@components/PageTitle'
import Pagination from '@components/Pagination'
import CardList from '@components/CardList'
import Divider from '@components/Divider'
import Seo from '@widgets/Seo'
import Sponsor from '@widgets/Sponsor'
import SponsorEvent from '@widgets/SponsorEvent'
import SponsorCommercial from '@widgets/SponsorCommercial'
import SponsorHuman from '@widgets/SponsorHuman'

const PageCollection = ({
  data: { posts, collectionInfo, featuredPosts = {} },
  ...props
}) => (
  <Layout {...props}>
    <Seo title={collectionInfo.name} description={collectionInfo.description} />
    <Divider />
    <Stack effectProps={{ effect: 'fadeInDown' }}>
      <PageTitle
        header={collectionInfo.name}
        subheader={collectionInfo.title}
        // running={collectionInfo.description}
        // totalCount={posts.totalCount}
      />
    </Stack>

    {/* <Divider /> */}
    <Stack>
      <Main>
        {posts.nodes && (
          <CardList
            nodes={posts.nodes}
            variant={['horizontal-md', 'vertical']}
            columns={[1, 2, 3, 3]}
            omitCategory={
              props.pageContext &&
              props.pageContext.collectionType === 'category'
            }
          />
        )}
      </Main>
    </Stack>
    <Divider />
    <PreFooter>
      <Pagination {...posts.pageInfo} {...collectionInfo} />
    </PreFooter>
    <Stack>
      <Sidebar pl={4}>
        <Divider />
        <Sponsor />
      </Sidebar>
      <Sidebar pl={4}>
        <Divider />
        <SponsorHuman />
      </Sidebar>
      <Sidebar pl={4}>
        <Divider />
        <SponsorEvent />
      </Sidebar>
      <Sidebar pl={4}>
        <Divider />
        <SponsorCommercial />
      </Sidebar>
    </Stack>
  </Layout>
)

export default PageCollection
